* {
  font-size: 16px;
}

body {
  margin: 2rem;
}

.container {
  display: grid;
  /* 3 x 2 
  grid-template-rows: repeat(3, 100px);
  grid-template-columns: repeat(2, 100px);
  
  grid-template: repeat(3, 100px) / repeat(2, 100px);
  */
  grid-template: 100px auto 100px/ 30fr 70fr;
  grid-template-areas:
    "header  header  "
    "sidebar main"
    ".       footer";
  gap: 10px;
  height: 100vh;
  border: 3px solid lightgray;
}

.box {
  background: rebeccapurple;
  color: white;
}

.box-one {
  /*
  grid-column: 1 / span 2;
  grid-row: 2;
  */
  /* start / end */
  /* row / column 
  grid-area: 1 / 1 / 1 / 3
  */
  grid-area: header;
}

.box-four {
  grid-area: footer;
  background: red;
}
